.register-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  // background-color: blue;
  // padding: 1rem;
  max-width: 1000px;
  margin: auto;

  .register-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    // color: white;
    max-width: 60%;
    background-color: #111;
    color: white;

    >* + * {
      margin-top: 1rem;
    }

    p {
      width: 60%;
      // background-color: rgb(233, 233, 233);
      background-color: #222;
      border-radius: 0.5rem;
      // color: white;
      padding: 1rem;
    }

    .heading {
      font-size: 2em;
      height: 50%;
      display: flex;
      align-items: center;
      align-self: center;
    }

    select {
      padding: 0.5rem;
      border-radius: 100rem;
      outline: none;
      font-size: 1em;
      width: 60%;
    }

    button {
      font-size: 1.1em;
      padding: 0.5rem 1rem;
      border-radius: 100rem;
      transition-duration: 300ms;
      width: 60%;
      &:hover {
        background-color: #ddd;
      }
      &.dormant {
        border-radius: 0.5rem;
      }
      &.dormant:hover {
        background-color: #eee;
        cursor: unset;
      }
      &+button {
        margin-left: 1rem;
      }
    }
  }
  div.error {
    // width: 100%;
    text-align: center;
  }
  .btn.error {
    padding: 0.5rem 2rem;
    display: inline-block;
  }
}

@media screen and (max-width: 820px) {
  .register-container {
    width: 100vw;
    .register-card {
      max-width: 80%;
    }
  }
}

@media screen and (max-width: 512px) {
  .register-container {
    height: 100vh;
    width: 100vw;
    .register-card {
      height: 100%;
      max-width: unset;
      width: 100vw;
      align-items: flex-start;
      border-radius: 0rem;
      p {
      }
      p, select {
        width: 100%;
      }
      .btn {
        width: 100%;
      }
    }
  }
}
